

export const format = (html: string): string => {
  const tab = '\t';
  let result = '';
  let indent = '';

  html.split(/>\s*</).forEach(element => {
    if (element.match(/^\/\w/)) {
      indent = indent.substring(tab.length);
    }

    result += indent + '<' + element + '>\r\n';

    if (element.match(/^<?\w[^>]*[^\/]$/)) {
      indent += tab;
    }
  });

  return result.substring(1, result.length - 3);
};

const generateCell = (cell: HTMLTableDataCellElement): string => {
  const attrs = [];
  if (cell.rowSpan > 1) {
    attrs.push(`rowspan="${cell.rowSpan}"`);
  }
  if (cell.colSpan > 1) {
    attrs.push(`colspan="${cell.colSpan}"`);
  }
  if (attrs.length > 0) {
    attrs.unshift('');
  }
  return `<td${attrs.join(' ')}>${cell.innerText.trim()}</td>`;
};

const generateRow = (element: HTMLTableSectionElement): string => {
  return Array.from(element.getElementsByTagName('tr'))
    .map(row => {
      return `<tr>${
        Array.from(row.getElementsByTagName('td'))
          .map(generateCell)
          .join('')
        }</tr>`;
    })
    .join('');
};

const generateBody = (element: HTMLTableSectionElement | null): string => {
  if (element == null) return '';
  return `<tbody>${generateRow(element)}</tbody>`;
};

const generateThead = (element: HTMLTableSectionElement | null): string => {
  if (element == null) return '';
  return `<thead>${generateRow(element)}</thead>`;
};

const areAttrsEmpty = (item: Element): boolean => {
  return item.attributes.length === 0;
};

const generateColgroup = (element: HTMLTableColElement | null): string => {
  if (element == null) return '';

  if (Array.from(element.children).every(areAttrsEmpty)) return '';

  return `<colgroup>${
    Array.from(element.children)
      .map(item => `<col ${Array.from(item.attributes).map(attr => `${attr.name}="${attr.value}"`).join(' ')}/>`)
      .join('')
    }</colgroup>`;
};

export const generateTable = (table: HTMLTableElement): string => {
  return `<table>
      ${generateColgroup(table.getElementsByTagName('colgroup').item(0))}
      ${generateThead(table.getElementsByTagName('thead').item(0))}
      ${generateBody(table.getElementsByTagName('tbody').item(0))}
    </table>`;
};