import { cx } from 'emotion';
import React, { useEffect, useRef, useState } from 'react';
import { EditorDirection } from '../../types';
import { CodeMirrorOptions } from './CodeMirrorWrapper';
import { createMarkdownEditor } from './createMarkdownEditor';
import { styles } from './style';

export type InitializedEditorType = ReturnType<typeof createMarkdownEditor>;

type MarkdownTextareaProps = {
  mode: 'default' | 'zen';
  direction: EditorDirection;
  disabled: boolean;
  visible: boolean;
  onReady: (editor: InitializedEditorType) => void;
};

function toCodeMirrorOptions(props: MarkdownTextareaProps): CodeMirrorOptions {
  if (props.mode !== 'zen') {
    return {
      direction: props.direction,
      readOnly: true,
    };
  }
  return {
    direction: props.direction,
    readOnly: true,
    fixedHeight: true,
    height: '100%',
  };
}
export const MarkdownTextarea = React.memo((props: MarkdownTextareaProps) => {
  const hostRef = useRef<HTMLDivElement>(null);
  const [editor, setEditor] = useState<InitializedEditorType | null>(null);

  useEffect(() => {
    if (hostRef.current) {
      setEditor(
        createMarkdownEditor(
          hostRef.current,
          toCodeMirrorOptions(props),
        )
      );
    }
  }, []);

  useEffect(() => {
    if (editor) {
      props.onReady(editor);
    }
  }, [editor]);

  const className = cx(
    styles.root,
    props.mode === 'default' ? styles.framed : styles.zen,
    props.disabled && styles.disabled
  );

  return (
    <div
      className={className}
      ref={hostRef}
      data-test-id="markdown-textarea"
      style={{ display: props.visible ? 'block' : 'none' }}
    />
  );
});

MarkdownTextarea.displayName = 'MarkdownTextarea';
