import '@contentful/forma-36-react-components/dist/styles.css';
import 'codemirror/lib/codemirror.css';
import { DialogExtensionSDK, FieldExtensionSDK, init, KnownSDK, locations } from 'contentful-ui-extensions-sdk';
import React from 'react';
import { render } from 'react-dom';
import { MarkdownEditor, renderMarkdownDialog } from './components';
import './index.scss';


function renderAtRoot(component: JSX.Element) {
  render(component, document.getElementById('root'));
}

function seeIfItFieldExtensionSDK(sdk: KnownSDK): sdk is FieldExtensionSDK {
  return sdk.location.is(locations.LOCATION_ENTRY_FIELD);
}

function seeIfItDialogExtensionSDK(sdk: KnownSDK): sdk is DialogExtensionSDK {
  return sdk.location.is(locations.LOCATION_DIALOG);
}

init(sdk => {
  if (seeIfItDialogExtensionSDK(sdk)) {
    renderAtRoot(renderMarkdownDialog(sdk as any));
  }
  if (seeIfItFieldExtensionSDK(sdk)) {
    sdk.window.startAutoResizer();
    renderAtRoot(<MarkdownEditor sdk={sdk} isInitiallyDisabled={false} />);
  }
});